*,
*::after,
*::before {
  box-sizing: border-box;
}

html,
body {
  width: 100%;
  height: 100%;
  user-select: none;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, sans-serif;
  color: var(--text);
}

::placeholder {
  opacity: .6;
  transition: opacity .2s;
}

:focus::placeholder {
  opacity: 0;
}

input,
textarea {
  background: transparent;
}

button.ui-button {
  padding: 3px 14px;
  color: var(--button-color);
  background: var(--button);
  border: 1px solid var(--button-border);
  box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.2);
  background-clip: border-box;
  font-size: 13px;
  border-radius: 5px;
}

button.ui-button.small {
  font-size: 11px;
  border-radius: 3px;
  padding: 2px 12px;
}

button.ui-button:focus {
  color: #fff;
  background: var(--accent);
  background-image: linear-gradient(var(--accent-light), var(--accent));
  border: 1px solid var(--accent);
  outline: none;
}

button.ui-button:active {
  color: #fff;
  background: var(--accent-dark);
  background-image: linear-gradient(var(--accent), var(--accent-dark));
  border: 1px solid var(--accent-dark);
}

button.ui-button:disabled {
  opacity: .5;
}

[data-theme^="dark"] ::placeholder {
  color: #fff;
}

main {
  font-size: 13px;
  position: fixed;
  top: 0;
  left: 200px;
  right: 0;
  bottom: 36px;
  z-index: 1;
  background: var(--main);
}

main h1 {
  text-align: center;
  font-size: 13px;
  font-weight: normal;
  line-height: 24px;
}

main #loader {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
  background: var(--loader) no-repeat center center;
}

aside {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  width: 200px;
  color: var(--side-text);
  border-right: 1px solid var(--dividers);
}

aside #sidebar {
  display: none;
}

aside #sidebar-all {
  position: absolute;
  top: 28px;
  left: 0;
  right: 0;
}

aside #sidebar-tags {
  position: absolute;
  top: 70px;
  left: 0;
  right: 0;
  bottom: 180px;
}

aside .sidebar-scroll {
  overflow-y: scroll;
  position: absolute;
  top: 60px;
  left: 0;
  right: 0;
  bottom: 0;
}

aside .list-title {
  display: block;
  font-size: 12px;
  line-height: 22px;
  padding: 0 20px 0 15px;
  font-weight: 500;
}

aside dl {
  font-size: 12px;
  line-height: 22px;
  padding: 0 10px;
}

aside dl#taglist-extras {
  margin-top: 11px;
  top: 20px;
  left: 0;
  right: 0;
}

aside dl dt {
  padding: 0 20px 0 15px;
  font-weight: 500;
}

aside dl dd {
  font-weight: 400;
  padding: 0;
}

aside dl dd a {
  display: flex;
  align-items: center;
  padding: 0 10px;
  color: var(--side-text);
  text-decoration: none;
  border-radius: 6px;
  transition: all .15s ease-in-out;
  -webkit-user-drag: none;
  cursor: default;
}

aside dl dd a:hover {
  background: var(--side-hover);
}

aside dl dd a .filter-icon {
  width: 25px;
  height: 22px;
  margin-right: 5px;
  background-color: var(--side-text);
}

aside dl dd a .filter-icon.icon-home {
  mask: var(--icon-home) no-repeat center right;
}

aside dl dd a .filter-icon.icon-untagged {
  mask: var(--icon-untagged) no-repeat center right 3px;
}

aside dl dd a .filter-name {
  flex-grow: 2;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  padding-right: 5px;
}

aside dl dd a.selected {
  background: var(--accent-light);
  color: #fff;
}

aside dl dd a.selected .filter-icon {
  background-color: #fff;
}

aside dl dd input[type='checkbox'] {
  margin-left: 10px;
}

aside dl#filter-all dd a, aside dl#taglist-extras dd a {
  padding-left: 0;
}

aside #column-list {
  width: 200px;
  position: absolute;
  bottom: 50px;
  left: 0;
  right: 0;
}

aside .search-field {
  height: 40px;
  padding: 8px 10px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

aside .search-field #search {
  color: var(--text-color);
  width: 100%;
  height: 22px;
  font-size: 13px;
  padding: 0 24px;
  background: #ffffff var(--icon-search) no-repeat left 5px center;
  border: 1px solid #b9b9b9;
  border-radius: 3px;
  box-shadow: inset 0 0 0 1px rgba(0, 0, 0, 0.05);
}

aside .search-field #clear {
  display: none;
  width: 20px;
  height: 20px;
  background: var(--icon-clear) no-repeat center;
  border: none;
  outline: none;
  position: absolute;
  top: 9px;
  right: 11px;
}

footer {
  background: var(--footer);
  position: absolute;
  left: 200px;
  right: 0;
  bottom: 0;
  height: 35px;
  z-index: 0;
  padding: 0 20px;
  display: flex;
  flex-direction: row-reverse;
  justify-content: flex-start;
  align-items: center;
}

footer #add-bookmark {
  height: 23px;
}

footer #add-bookmark svg polygon {
  fill: var(--button-color);
}

footer #add-bookmark:focus svg polygon, footer #add-bookmark:active svg polygon {
  fill: #fff;
}

footer #update,
footer #dev {
  white-space: nowrap;
  height: 15px;
  font-size: 11px;
  font-weight: 500;
  color: #fff;
  border: 0;
  border-radius: 3px;
  margin-right: 10px;
}

footer #update {
  background-color: #6236FF;
}

footer #update:active {
  background-color: #9164FF;
}

footer #update:focus {
  outline: none;
}

footer #dev {
  padding: 1px 6px;
  margin-top: 1px;
  background-color: #18bf64;
}

footer #dev.α {
  background-color: #fb434a;
}

footer #dev.β {
  background-color: #007cff;
}

footer .fadein {
  display: none;
}

.tag {
  display: inline-block;
  width: 12px;
  height: 12px;
  border: 1px solid transparent;
  border-radius: 50%;
  overflow: hidden;
  text-indent: -1000px;
  margin: 0 2px;
}

.tagname {
  font-size: 11px;
  display: inline-block;
  margin-right: 10px;
}

aside .tag {
  width: 10px;
  height: 10px;
  margin-right: 10px;
}

.selected .tag {
  border-color: #fff;
}

.selected .tag.untagged {
  border: 0;
  border-radius: 0;
  background-color: #fff;
  mask: var(--icon-untagged) no-repeat center center;
}

#bookmarks tbody tr td {
  cursor: default;
}

#bookmarks tbody tr td.details-control {
  background: var(--disclosure-closed) no-repeat center;
}

#bookmarks tbody tr.shown td.details-control {
  background-image: var(--disclosure-opened);
}

#bookmarks tbody tr.selected td.details-control {
  background-image: var(--disclosure-closed-focus);
}

#bookmarks tbody tr.selected.shown td.details-control {
  background-image: var(--disclosure-opened-focus);
}

#bookmarks .tag {
  text-indent: 1000px;
}

#bookmarks .tags-column .tag {
  margin-bottom: 1px;
}

#bookmarks .tags-column .tagname {
  display: none;
}

.padded-left {
  padding-left: 20px !important;
}

.padded-right {
  padding-right: 20px !important;
  padding-left: 20px !important;
}

td.padded-right {
  display: flex;
  justify-content: flex-end;
}

#toggle-info-panel {
  appearance: none;
  border: 0;
  width: 15px;
  height: 12px;
  background-color: #868686;
  mask: var(--icon-details) no-repeat center center;
}

#toggle-info-panel.opened {
  background-color: var(--text);
}

.details-panel {
  margin: -4px -10px;
  padding: 10px 20px 20px 45px;
  line-height: 1.4;
  border-top: 1px solid var(--table-border);
}

.details-panel .row {
  padding: 5px 0;
  min-height: 28px;
  position: relative;
  border-bottom: 1px solid var(--details-divider);
}

.details-panel .row .label {
  float: left;
  width: 100px;
  padding-left: 10px;
}

.details-panel .row .value {
  margin-left: 100px;
  padding-right: 10px;
}

.details-panel .row .nowrap {
  position: absolute;
  left: 0;
  right: 0;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.details-panel .row .wrap {
  white-space: normal;
}

.details-panel .row .tag {
  vertical-align: -2px;
}

.details-panel .row .tag.untagged {
  background-color: var(--side-text);
  mask: var(--icon-untagged) no-repeat center center;
}

.details-panel .row .favicon {
  vertical-align: text-bottom;
  margin-right: 5px;
}

.details-panel .row [id^=url_] {
  color: var(--text);
  text-decoration: none;
  transition: color .4s;
  cursor: default;
}

.details-panel .row [id^=url_]:hover {
  color: royalblue;
}

.details-panel .buttons {
  padding-top: 15px;
}

.details-panel .buttons button {
  margin-right: 5px;
}

[data-theme="dark"] aside .search-field #search {
  background-color: rgba(255, 255, 255, 0.1);
  border-color: rgba(255, 255, 255, 0.1);
  box-shadow: inset 0 2px 2px rgba(0, 0, 0, 0.1);
}

[data-theme="dark"] .tag {
  filter: invert(100%);
}

[data-theme="dark"] .selected .tag {
  border-color: #000;
}

[data-theme="dark"] .selected .tag.untagged {
  border: 0;
  border-radius: 0;
  background-color: #000;
  mask: var(--icon-untagged) no-repeat center center;
}

[data-theme="dark"] .details-panel .row .tag.untagged {
  background-color: #000;
}

[data-theme="dark"] .details-panel .row [id^=url_]:hover {
  color: dodgerblue;
}

table.dataTable {
  width: 100%;
  margin: 0 auto;
  clear: both;
  border-collapse: separate;
  border-spacing: 0;
}

table.dataTable thead {
  background: var(--tabs);
  color: var(--text);
}

table.dataTable thead th,
table.dataTable tfoot th {
  font-weight: 500;
  padding: 10px 18px;
  border-bottom: 1px solid var(--tab-dividers);
  position: relative;
}

table.dataTable thead th::after,
table.dataTable tfoot th::after {
  display: block;
  content: '';
  width: 1px;
  background-color: var(--tab-dividers);
  position: absolute;
  top: 4px;
  bottom: 4px;
  right: 0;
}

table.dataTable thead th:last-of-type::after,
table.dataTable tfoot th:last-of-type::after {
  display: none;
}

table.dataTable thead th:active,
table.dataTable thead td:active {
  outline: none;
}

table.dataTable tfoot td {
  padding: 10px 18px 6px 18px;
  border-top: 1px solid var(--table-border);
}

table.dataTable thead .sorting,
table.dataTable thead .sorting_asc,
table.dataTable thead .sorting_desc,
table.dataTable thead .sorting_asc_disabled,
table.dataTable thead .sorting_desc_disabled {
  cursor: default;
  background-repeat: no-repeat;
  background-position: center right 10px;
}

table.dataTable thead .sorting {
  background-image: none;
}

table.dataTable thead .sorting_asc {
  background-image: var(--sort-up);
}

table.dataTable thead .sorting_desc {
  background-image: var(--sort-down);
}

table.dataTable thead .sorting_asc_disabled {
  background-image: none;
}

table.dataTable thead .sorting_desc_disabled {
  background-image: none;
}

table.dataTable tbody th,
table.dataTable tbody td {
  padding: 8px 10px;
  color: var(--table-text);
}

table.dataTable.nowrap td {
  white-space: nowrap;
}

table.dataTable.stripe tbody tr.odd, table.dataTable.display tbody tr.odd {
  background-color: var(--stripe-odd);
}

table.dataTable.stripe tbody tr.even, table.dataTable.display tbody tr.even {
  background-color: var(--stripe-even);
}

table.dataTable.stripe tbody tr.odd.selected,
table.dataTable.display tbody tr.odd.selected,
table.dataTable.stripe tbody tr.even.selected,
table.dataTable.display tbody tr.even.selected {
  background-color: var(--accent);
}

table.dataTable.hover tbody tr:hover, table.dataTable.display tbody tr:hover {
  background-color: var(--select-sort);
}

table.dataTable.hover tbody tr:hover td, table.dataTable.display tbody tr:hover td {
  color: var(--text);
}

